*{
    margin:0;
    padding:0;
}

/* Navigation CSS */
.nav-main{
    width:100vw;
    background-color: rgb(32, 33, 35);
    /* padding: 1cm 0.5cm; */
    margin:0;
}

.nav-child{
    padding:0.5cm;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.nav-col-2{
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 0.8cm;
}

/*Calendar CSS */

.calendar-main{
    width: 100%;   
}

.calendar-child{
    display: flex;
    flex-direction: column;
    row-gap: 0.2cm;
    margin:0.8cm 1cm;
    border-radius: 15px;
}

img{
    padding: 0;
    margin: 0;
}

.icon{
    width:0.6cm;
}

.icon-active{
    /* width:fit-content; */
    background-color: #855aa7;
    border-radius: 30px;
    max-height:1.3cm;
    display: flex;
    padding: 0.1cm 0.3cm;
    flex-direction: row;
    align-items: center;
    column-gap:0.3cm;
    color:white;
    margin:0;
}

.icon-inactive{
    background-color: rgb(66, 51, 78);
    border-radius: 30px;
    max-height:1.3cm;
    display: flex;
    padding: 0.15cm 0.2cm;
    flex-direction: row;
    align-items: center;
    column-gap:0.3cm;
    color:white;
    margin:0;
}

.calendar-row-1{
    display: flex;
    flex-direction: row;
    column-gap: 0.2cm;
}

.calendar-row-2{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    column-gap: 1cm;
    padding: 0 0.5cm;
    bottom:0;
}

.calendar-row-2-col-1{
    background-color: rgb(32,33,37);
    border-radius: 0px 35px 0px 45px;
    padding:0.5cm 1cm;
    border: thin solid #855aa7;
    width: 20cm;
}

.calendar-row-2-col-2{
    background-color: rgb(32,33,37);
    border-radius: 10px;
    padding:0.2cm;
    border: thin solid #1a1a1a;
    width: 8cm;
    min-height: 60vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    row-gap: 0.1cm;
}

.cont{
    background-color: rgb(168, 104, 0);
    padding: 0.2cm;
    border-radius: 12px;
    display: flex;
    flex-direction: row;
    column-gap: 0.cm;
}

.cont-col-1{
    border-right: 2px solid #1a1a1a;
    padding: 0.1cm 0.2cm;
}
.cont-col-2{
    text-wrap: wrap;
    padding: 0.1cm 0.2cm;
}

.dateHead{
    font-size: 22px;
    font-weight: bold;
    text-align: flex-end;
}
.subHead{
    color:rgb(201, 201, 201);
    font-size: 12px;
}

.task-head{
    background-color: #1e1e1e;
    padding:0.2cm 0;
}

.task-text{
    font-size: 24px;
    font-weight: bold;
    text-align: flex-end;
}
.green{
    background-color: rgb(30, 149, 0);
}

.brown{
   background-color: rgb(168, 104, 0); 
}

.blue{
    background-color: rgb(0, 91, 147);
}

.pink{
    background-color: rgb(160, 0, 99);
}

.red{
    background-color: rgb(138, 1, 1);
}

.calendar-tile{
    width:2cm;
    max-width: 2cm;
    height:1.5cm;
    max-height: 2cm;
    padding: 0.2cm;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: end;
    border-radius: 5px;
    background-color: rgb(32, 33, 40);
    box-shadow: rgba(152, 152, 152, 0.426) 0px 2px 8px 0px;
}

.calendar-tile:hover{
    box-shadow: rgba(149, 149, 149, 0.475) 1px 3px 9px 1px;
}

.calendar-parent{
    border-radius: 10px;
    padding: 0.8cm;
    display: flex;
    flex-direction: row;
    gap: 0.3cm; 
    flex-wrap: wrap;

}
